import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/App.css';
import Tappable from "@vkontakte/vkui/dist/components/Tappable/Tappable";

export class IconButton extends PureComponent {

    render() {
        return this.props.mode === 'small' ?
            <Tappable style={{ borderRadius: '50%', padding: '8px' }}>
                {this.props.icon}
            </Tappable>
            :
            <Tappable style={{ borderRadius: '50%', padding: '12px' }}>
                {this.props.icon}
            </Tappable>
    }

}